.page-header {
  background: #1d2631 url("../../img/header-bg.jpg");
  color: $color-white;
}

.page-header__main {
  background-color: rgba(0, 0, 0, 0.3);
  padding-top: 22px;
  padding-bottom: 22px;
  width: 100%;

  @media (min-width: $desktop-width) {
    padding-bottom: 40px;
  }
}

.page-header__main-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}

.page-header__logo--full {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
    width: 147px;
  }
}

.page-header__additional {
  padding-top: 30px;

  &--index {
    background: url("../../img/hand-phone-mob.png") no-repeat top 130px left;
    background-size: 307px 273px;
    height: 664px;
  }

  &--catalog {
    padding-bottom: 50px;
  }
}

@media (min-width: $tablet-width) {
  .page-header__additional {
    padding-top: 74px;

    &--index {
      background: url("../../img/hand-phone-tablet.png") no-repeat bottom right;
      background-size: 660px 545px;
      height: auto;
    }
  }
}

.page-header__container {
  max-width: 1200px;
  margin: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: $desktop-width) {
  .page-header__additional {
    padding-top: 114px;

    &--index {
      background: url("../../img/hand-phone-desktop.png") no-repeat bottom right;
      background-size: 1050px 645px;
      height: auto;
    }
  }

  .page-header__container {
    padding-left: 80px;
  }
}

.page-header__title {
  @include padding-lr (40px, 40px);
  margin: 0;
  font-size: 24px;
  line-height: normal;
  font-weight: 300;
  text-align: center;

  @media (min-width: $tablet-width) {
    @include padding-lr (50px, 50px);
    width: 245px;
    margin-bottom: 39px;
    font-size: 40px;
    line-height: 120%;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 51px;
    font-size: 50px;
    line-height: normal;
  }
}

@media (min-width: 1440px) {
  .page-header__additional--index {
    background: url("../../img/hand-phone-desktop.png") no-repeat bottom right 20%;
    background-size: 1050px 645px;
    height: auto;
  }
}
