@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: light;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding-left: 20px;
  padding-right: 20px;
  overflow: hidden;
  position: relative;
}
@media (min-width: 1200px) {
  .wrapper {
    max-width: 1092px;
    margin: auto;
  }
}

.toggle {
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1200px) {
  .toggle {
    display: none;
  }
}

.button {
  border-radius: 1px;
  border: 2px solid #2aab6d;
  background: #2aab6d;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 10px;
  text-transform: uppercase;
  padding: 23px 45px;
}

.slider {
  position: relative;
}

.slider__buttons-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 14px;
}

.slider__buttons-item {
  margin-right: 19px;
  margin-bottom: 5px;
}

.slider__button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  width: 10px;
  height: 10px;
  border: none;
  background-color: transparent;
  display: flex;
}

.slider__icon-bullet {
  fill: rgba(40, 54, 69, 0.3);
  margin: auto;
}

.slider__button--current .slider__icon-bullet {
  border-radius: 6px;
  border: 3px solid #283645;
  fill: #ffffff;
}

.slider__arrow {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  position: absolute;
}

.slider__arrow--left {
  top: 20px;
  left: 0;
}

.slider__arrow--right {
  top: 20px;
  right: 0;
}

.slider__arrow-icon {
  fill: #283645;
  opacity: 0.3;
}

.table {
  width: 828px;
  border-collapse: collapse;
}
@media (min-width: 660px) {
  .table {
    width: 100%;
  }
}

.table__header {
  background-color: #283645;
  color: #ffffff;
}

.table__header__cell {
  border-radius: 1px 0 0 0;
  border: 2px solid #e5e5e5;
  border-top-color: #283645;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
  padding: 8px 15px 20px;
  box-sizing: border-box;
}

.table__header__cell--hidden {
  visibility: hidden;
}

.table__header__cell--ribboned {
  position: relative;
}
.table__header__cell--ribboned::before {
  position: absolute;
  top: -1px;
  left: -1px;
  content: url("../../img/hit-ribbon.svg");
}
.table__header__cell--ribboned::after {
  position: absolute;
  top: 13px;
  left: 13px;
  content: "хит";
  color: #ffffff;
  text-align: center;
  font-weight: 400;
  line-height: 30px;
  transform: rotate(-45deg);
}

.table__cell__text-only {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-transform: none;
}
@media (max-width: 659px) {
  .table__cell__text-only {
    display: none;
  }
}

.table__header__title {
  font-size: 16px;
  font-weight: 700;
}

.table__header__price {
  font-size: 18px;
  font-weight: 300;
}

.table__cell {
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
  color: #283645;
  font-weight: 400;
  line-height: 27px;
  text-transform: uppercase;
  padding-left: 20px;
  box-sizing: border-box;
  position: relative;
  padding-top: 14px;
  padding-bottom: 14px;
}
@media (min-width: 660px) {
  .table__cell {
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

@media (min-width: 660px) {
  .table__cell__text {
    display: none;
  }
}

.table__cell__picture {
  position: absolute;
  right: 19px;
}
.table__cell__picture--yes {
  right: 17px;
}
@media (min-width: 660px) {
  .table__cell__picture {
    right: 50%;
    transform: translateY(-50%);
  }
}

